@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

/* Configuração da primeira seção*/
.section1{
    background: url("/img/grupo-de-pessoas-trabalhando-juntas-azul.png") no-repeat;
    background-color: #62bcff;
    background-size: contain;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around; 
    flex-wrap: wrap; 
}
.textSection1{
    display: flex;
    flex-direction: column;
}                 
.designSpanH1{
    background-color: rgb(54,79,120);
    color: #ffffff;
}
.designSpanH2{
    color: #308d91;
    background-color: #ffff;
}
.formSection1{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap ;
    background-color: #efefef; 
} 
.designSpanformH2{
    text-align: center;
}    
.formSection1 input[type="text"],
.formSection1 input[type="email"],
.formSection1 input[type="text"]{
    position: relative;
    border: none;
} 
.formSection1 input[type="button"]{
    background-color: #364f78;
    color: white;
    cursor: pointer;
    border: none;
}
    .formSection1 input[type="button"]:focus{ 
        outline: none;
    }   
.section2{
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-content: space-around;
    position: relative;
}
.text1Sec2{
    display: flex;
}  
.text1Sec2 img{
    position: absolute;
} 
.section3{
    display: flex;
    background-color: #59aae7 ;
    flex-direction: column;
    flex-wrap: wrap;
} 
.section3 p{
    color: seashell;
    background-color:rgb(54,79,120) ;
}  
.buttonSec3 input[type='button']{
    color: rgb(54,79,120);
    background-color: #efefef ;
    border-radius: 28px;
    cursor: pointer;
}
.buttonSec3 input[type='button']:focus{
    outline: none;
} 
.footer{
    display: flex;
    margin: auto;
    align-items: center;
    background-color: #59aae7 ;
    justify-content: center;
    
}
.footer a{
    color: black;
    text-decoration: none;
    
} 
    .footer a:hover{
        color: seashell;
        background-color:rgb(54,79,120);
        transition-delay: 0.5s;
    }

